:root {
  --off-white: #f9f9f9;
  --charcoal: #323131;
  --grey: #c7c6c6;
  --light-grey: #f2f2f2;
  --green: #62a062;
  --yellow: #f5bb24;
  --purple: #816c8e;
  --red: #c44b33;
  --blue: #495990;
  --light-blue: #479fa7;
  --orange: #ffb66b;

  --main-font: 'Open Sans', cursive;
  --slab-font: 'Roboto Slab', serif;

  --box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.28);

  --confirm: #62a062;
  --danger: #c44b33;
}

body {
  box-sizing: border-box;
  min-height: 100vh;
  color: var(--charcoal);
  background-color: var(--off-white);
  font-family: var(--main-font);
}
